/**
 * External dependencies
 */
import tippy, { hideAll } from "tippy.js";
import 'tippy.js/dist/tippy.css';
/**
 * WordPress dependencies
 */
import { applyFilters } from "@wordpress/hooks";
/**
 * Internal dependencies
 */
import defaultTemplate from "./default.handlebars";
import "./index.scss";

export default (selector, baseUrl, tippyOptions = {}) => {
  let triggers = document.querySelectorAll(selector);

  let tippyDefaultOptions = {
    animateFill: false,
    inertia: false,
    arrow: true,
    interactive: true,
    theme: "wordlift",
    // Enable this to debug using Developer Tools (tippy won't hide).
    // hideOnClick: false,
    // trigger: "click",
  };

  triggers.forEach((trigger) => {
    trigger.addEventListener(
      "mouseover",
      function (event) {
        const href = trigger.getAttribute("href");
        const fn = applyFilters(
          "wl_context_cards_load_fn_supplier",
          (url, elem) => {
            const join = -1 === url.indexOf("?") ? "?" : "&";
            const ids = elem
              .getAttribute("data-id")
              .split(";")
              .map((p) => encodeURIComponent(p));
            const param = "id[]=" + ids.join("&id[]=");

            return fetch(`${url}${join}${param}&website=1`).then((response) =>
              response.json()
            );
          }
        );

        fn(baseUrl, trigger).then((json) => {
          const parsedContent = defaultTemplate({
            context: { href },
            data: json,
          });
          if (parsedContent) {
            let tippyInstance = tippy(trigger, {
              ...Object.assign(tippyDefaultOptions, tippyOptions, {
                content: parsedContent,
                allowHTML: true,
              }),
            });
            hideAll({ duration: 0 });
            tippyInstance.show();
            // if (tippyInstance) {
            //   hideAll({ duration: 0 });
            //   tippyInstance.show();
            // }
          }
        });
      },
      false
    );
  });
};
